import React from "react";

const TableField = ({ fieldName, settingField, removeBook, editBooks }) => {
  return (
    <div className="tableField">
      {fieldName}
      {settingField && (
        <>
          <i onClick={editBooks} className="fas fa-edit"></i>
          <i onClick={removeBook} className="far fa-trash-alt"></i>
        </>
      )}
    </div>
  );
};

export default TableField;
